@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

:root {
  --mainBlack: #000;
  --mainWhite: #fff;
  --overImageBlack: rgba(0, 0, 0, 0.3);
  --lightPurple: rgb(202, 113, 219);
  --lighBLue: #18cbb4;
  --lighterBlue: #12d7a0;
  --turquoise: #45e081;
  --lightGreen: #76e758;
  --yellowGreen: #a8eb12;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: var(--mainBlack);
}

.loading {
  width: 6rem;
  height: 6rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* Main Logo */

.main_logo {
  position: absolute;
  width: 1rem;
  height: 1rem;
  margin: 3rem;
  animation-name: go_logo;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  z-index: 100;
}

/* Welcome div */

.welcome {
  width: 100%;
  height: 100vh;
  background: linear-gradient(var(--overImageBlack), var(--overImageBlack)),
    url("./Images/wrapper_cover.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.welcome > h1 {
  position: absolute;
  font-size: 4rem;
  color: white;
  white-space: nowrap;
  left: 50%;
  top: 40%;
  transform: translate(-300%, -40%);
  -webkit-transform: translate(-300%, -40%);
  -moz-transform: translate(-300%, -40%);
  animation-name: go_welcome_h1;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}

.welcome_btn {
  border: none;
  padding: 0.8rem;
  border-radius: 20px;
  font-size: 1.4rem;
  background: linear-gradient(
    to bottom,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  background: -moz-linear-gradient(
    top,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  background: -webkit-linear-gradient(
    top,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  color: var(--mainWhite);
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(250%, -55%);
  -webkit-transform: translate(250%, -55%);
  -moz-transform: translate(250%, -55%);
  animation-name: go_welcome_btn;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-delay: 2s;
  animation-fill-mode: forwards;
}

.welcome_btn:hover {
  cursor: pointer;
  background: linear-gradient(
    to top,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  background: -moz-linear-gradient(
    bottom,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  background: -webkit-linear-gradient(
    bottom,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
}

/* Main Div Cards */

.main_div_cards {
  width: 90%;
  height: 90vh;
  margin: 0rem auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.main_div > h2 {
  position: absolute;
  left: 50%;
  top: 92%;
  transform: translate(-50%, -2%);
  -webkit-transform: translate(-50%, -2%);
  -moz-transform: translate(-50%, -2%);
  color: var(--lightPurple);
  white-space: nowrap;
}

.random_card {
  width: 20rem;
  height: 80%;
  border-radius: 10px;
  border: 1px solid var(--yellowGreen);
  margin: 1rem;
  transition: 2s;
  color: var(--mainWhite);
  font-size: 2rem;
  font-weight: bold;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.random_card:hover {
  padding-bottom: 10rem;
  font-size: 4rem;
}

.random_card:nth-child(1) {
  background: url("./Images/food_card.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0;
  animation-name: go_cards;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}

.random_card:nth-child(2) {
  background: url("./Images/sports_card.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0;
  animation-name: go_cards;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 2s;
}

.random_card:nth-child(3) {
  background: linear-gradient(var(--overImageBlack), var(--overImageBlack)),
    url("./Images/science_card.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0;
  animation-name: go_cards;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 3s;
}

.random_card:hover {
  cursor: pointer;
  transform: scale(1.05);
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
}

/* Jokes Page */

.jokes_page {
  width: 100%;
  height: 100vh;
  overflow: auto;
}

.new_jokes_container {
  width: 100%;
  height: 100%;
  margin: 2rem auto;
  display: grid;
  grid-template-rows: 15% 85%;
  border-radius: 5px;
}

.new_jokes_container > h2 {
  text-align: center;
  color: white;
  width: 20rem;
  height: 2rem;
  margin: 0rem auto;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.new_cards_container {
  width: 80%;
  height: 20rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin: 0rem auto;
  text-align: center;
}

.seen_jokes_container {
  width: 80%;
  margin: 1rem auto;
  height: auto;
  display: grid;
  grid-template-rows: 8rem auto;
}

.seen_cards_container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.seen_jokes_container > h2 {
  text-align: center;
  color: white;
  width: 20rem;
  height: 2rem;
  margin: 2rem auto;
  border-radius: 5px;
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.jokes_card {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  color: var(--mainWhite);
  padding-top: 1rem;
  font-weight: bold;
}

.food_card_style {
  max-width: 15rem;
  height: auto;
  border-radius: 10px;
  padding: 4px;
  font-size: 0.9rem;
  background: linear-gradient(
    to bottom,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  background: -moz-linear-gradient(
    top,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  background: -webkit-linear-gradient(
    top,
    var(--lighBLue),
    var(--lighterBlue),
    var(--turquoise),
    var(--lightGreen),
    var(--yellowGreen)
  );
  box-shadow: 1px 1px 1px black;
}

.sport_card_style {
  font-size: 0.9rem;
  max-width: 15rem;
  height: auto;
  padding: 4px;
  background: linear-gradient(
    to bottom,
    rgb(2, 0, 36) 0%,
    rgba(9, 9, 121) 35%,
    rgba(0, 212, 255) 100%
  );
  border-radius: 10px;
  box-shadow: 1px 1px 1px black;
}

.science_card_style {
  max-width: 15rem;
  height: auto;
  border-top: 1px solid var(--mainWhite);
  border-bottom: 1px solid var(--mainWhite);
  background: linear-gradient(
    to bottom,
    rgba(2, 0, 36, 1) 0%,
    rgba(250, 0, 0, 1) 35%
  );
  border-radius: 10px;
  box-shadow: 1px 1px 1px black;
  padding: 4px;
}

.foodClass {
  background: url("./Images/food_page.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.sportClass {
  background: url("./Images/sport_page.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.scienceClass {
  background: url("./Images/science_page.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.foodJokesH2 {
  background-color: rgba(202, 113, 219, 0.8);
}

.seenFoodJokesH2 {
  background-color: rgba(168, 235, 18, 0.8);
}

.sportJokesH2 {
  background-color: rgba(34, 0, 224, 0.8);
}

.seenSportJokesH2 {
  background-color: rgba(233, 235, 255, 0.8);
}

.scienceJokesH2 {
  background-color: rgba(247, 3, 3, 0.8);
}

.seenScienceJokesH2 {
  background-color: rgba(255, 147, 147, 0.8);
}

/* Media Queris */

@media screen and (max-width: 1010px) {
  /* Welcome page */

  .welcome > h1 {
    font-size: 3rem;
  }

  .welcome_btn {
    font-size: 1.2rem;
  }

  /* Cards */

  .new_cards_container {
    grid-template-columns: repeat(4, 1fr);
    margin: 2rem auto;
  }

  .seen_cards_container {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (max-width: 860px) {
  /* Cards */

  .new_cards_container {
    grid-template-columns: repeat(3, 1fr);
    margin: 2rem auto;
  }

  .seen_cards_container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 760px) {
  /* Welcome Page */

  .welcome > h1 {
    font-size: 2.5rem;
  }

  .welcome_btn {
    font-size: 1rem;
    left: 50%;
    top: 52%;
    transform: translate(250%, -52%);
    -webkit-transform: translate(250%, -52%);
    -moz-transform: translate(250%, -52%);
  }

  /* Main Div */

  .main_div > h2 {
    top: 95%;
  }

  .main_div_cards {
    flex-direction: column;
    margin: 2rem auto;
  }

  .random_card:hover {
    padding-bottom: 4rem;
    font-size: 3rem;
  }

  /* Cards page */

  .jokes_card {
    font-size: .9rem;
  }

}

@media screen and (max-width: 680px) {
  /* Cards */

  .new_jokes_container {
    grid-template-rows: 5% 95%;
  }

  .new_cards_container {
    grid-template-columns: repeat(2, 1fr);
    margin: 2rem auto;
  }

  .seen_cards_container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 600px) {
  /* Main Logo */

  .main_logo {
    width: 0.8rem;
    height: 0.8rem;
    margin: 2rem;
  }
}

@media screen and (max-width: 500px) {
  /* Main Logo */

  .main_logo {
    display: none;
  }
}

@media screen and (max-width: 450px) {
  /* Main Div */

  .main_div > h2 {
    font-size: 1.2rem;
  }

  /* Cards page */

  .new_jokes_container {
    height: 120%;
    grid-template-rows: 10% 90%;
  }

  .new_cards_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 7rem;
  }

  .seen_cards_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; 
  }

  .seen_jokes_container > h2 {
    margin-top: 3rem;
  }
}

@media screen and (max-width: 400px) {

  .new_jokes_container {
    height: 120%;
    grid-template-rows: 5% 95%;
  }

  .new_jokes_container > h2 {
    font-size: 1rem;
    width: 15rem;
  }

  .new_cards_container {
    margin-top: 8rem;
  }

  .seen_jokes_container > h2 {
    font-size: 1rem;
    width: 15rem;
    margin-top: 2rem;
  }
}

@media screen and (max-width: 350px) {
  /* Main Div */

  .main_div > h2 {
    font-size: 1rem;
  }

  .random_card {
    width: 15rem;
    font-size: 2rem;
  }

  .new_jokes_container > h2 {
    width: 10rem;
  }

  .seen_jokes_container > h2 {
    width: 12rem;
    margin-top: 5rem;
  }
  

  /* Cards */

}

@media screen and (max-width: 300px) {
  /* Welcome Page */

  .welcome > h1 {
    font-size: 2rem;
  }

  .welcome_btn {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 250px) {
  /* Welcome Page */

  .welcome > h1 {
    font-size: 1.8rem;
  }

  .welcome_btn {
    font-size: 0.6rem;
    padding: 0.6rem;
  }
}

/* KEY FRAMES */

@keyframes go_logo {
  0% {
    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
  }

  90% {
    transform: scale(7);
    -webkit-transform: scale(7);
    -moz-transform: scale(7);
  }

  100% {
    transform: scale(5);
    -webkit-transform: scale(5);
    -moz-transform: scale(5);
  }
}

@keyframes go_welcome_h1 {
  0% {
    transform: translateX(-300%);
    -webkit-transform: translateX(-300%);
    -moz-transform: translateX(-300%);
  }

  90% {
    transform: translateX(-55%);
    -webkit-transform: translateX(-55%);
    -moz-transform: translateX(-55%);
  }

  95% {
    transform: translateX(-45%);
    -webkit-transform: translateX(-45%);
    -moz-transform: translateX(-45%);
  }

  100% {
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
  }
}

@keyframes go_welcome_btn {
  0% {
    transform: translateX(300%);
    -webkit-transform: translateX(300%);
    -moz-transform: translateX(300%);
  }

  90% {
    transform: translateX(-55%);
    -webkit-transform: translateX(-55%);
    -moz-transform: translateX(-55%);
  }

  95% {
    transform: translateX(-45%);
    -webkit-transform: translateX(-45%);
    -moz-transform: translateX(-45%);
  }

  100% {
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
  }
}

/* Main Div */

@keyframes go_cards {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
